<template>
  <div class="realtime-setting">
    <v-btn color="success" outlined v-if="scheduler" @click="connectScheduler"
      >  </v-btn
    >

    <v-btn color="error" outlined v-if="!scheduler" @click="disconnectScheduler"
      >  </v-btn
    >
    <v-btn
      color="primary"
      style="margin-top:10px"
      outlined
      @click="checkConnectScheduler"
      >   </v-btn
    >
  </div>
</template>

<script>
import axios from "axios";
import SERVER from "@/api/spring.js";
export default {
  data() {
    return {
      scheduler: true
    };
  },
  created() {
    axios
      .get(SERVER.URL + SERVER.ROUTES.scheduleStatus)
      .then((res) => {
        if (res.data.result == "running") {
          this.scheduler = false;
          console.log("     .");
        } else if (res.data.result == "end") {
          this.scheduler = true;
          console.log("     .");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    //   (  )
    connectScheduler() {
      axios
        .get(SERVER.URL + SERVER.ROUTES.scheduleStart)
        .then((res) => {
          if (res.data.result == "success") {
            alert("   .");
            this.scheduler = false;
          } else if (res.data.result == "fail") {
            alert(
              "    ...   ..."
            );
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    checkConnectScheduler() {
      axios
        .get(SERVER.URL + SERVER.ROUTES.scheduleStatus)
        .then((res) => {
          if (res.data.result == "running") {
            alert("     .");
          } else if (res.data.result == "end") {
            alert("     .");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    disconnectScheduler() {
      axios
        .get(SERVER.URL + SERVER.ROUTES.scheduleStop)
        .then((res) => {
          if (res.data.result == "success") {
            alert("     !!");
            this.scheduler = true;
          } else if (res.data.result == "fail") {
            alert(
              "    ...   ..."
            );
          }
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }
};
</script>

<style>
.realtime-setting {
  display: flex;
  flex-direction: column;
}
.realtime-setting .v-btn {
  font-size: 12px;
  font-weight: bold;
}
</style>
